import { graphql, useStaticQuery } from 'gatsby';
import type { FluidObject } from 'gatsby-image';

type Parent = {
  excerpt: string;
};

type Node = {
  parent: Parent;
  title: string;
  slug: string;
  author: string;
  path: string;
  image: {
    sharp: { fluid: FluidObject };
  };
  date: string;
};

type Data = {
  allDevlog: {
    nodes: Node[];
  };
};

export type Post = Omit<Node, 'parent'> & Pick<Parent, 'excerpt'>;

export const usePosts = () => {
  const data = useStaticQuery<Data>(graphql`
    query {
      allDevlog(sort: { fields: date, order: DESC }, limit: 3) {
        nodes {
          author
          title
          slug
          date
          path
          image {
            sharp: childImageSharp {
              fluid(maxWidth: 100, maxHeight: 100) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
          parent {
            ... on Mdx {
              excerpt(pruneLength: 110)
            }
          }
        }
      }
    }
  `);

  const posts: Post[] = data.allDevlog.nodes.map(({ parent: { excerpt }, ...fields }) => ({
    excerpt,
    ...fields,
  }));

  return posts;
};
